import React, { DOMAttributes } from 'react';
import { IRestProps, mergeRestProps } from '../../common/react/mergeRestProps';
import { NavBarItemBox, IAlign } from './styled';

export interface INavBarItemProps extends IRestProps, DOMAttributes<HTMLDivElement> {
  /**
   * 子节点
   */
  children?: React.ReactNode;
  /**
   * 对齐方式
   */
  align: IAlign;
}

/**
 * 导航栏元素，控制元素大小
 */
export const NavigationBarMobileItem = React.memo(
  (props: INavBarItemProps) => {
    const { align, ...rest } = props;

    return (
      <NavBarItemBox align={align} {...mergeRestProps(rest, 'wdn-navbar-item')}>
        { props.children }
      </NavBarItemBox>
    );
  },
);
